import React from "react"
import { Link } from "gatsby"

import { Navbar, Nav } from "react-bootstrap"

const CustomNavbar = ({ pageInfo }) => {
  console.log(pageInfo)
  return (
    <><header class="sticky-top flex-md-nowrap p-0">
      <Navbar variant="dark" expand="sm" id="site-navbar" style={{background: `#20232a`, padding: `0.765rem 2.0875rem`,}}>
        <Link to="/">
          <Navbar.Brand as="span">   </Navbar.Brand>
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto" activeKey={pageInfo && pageInfo.pageName}>
            <Link to="/page-2" style={{textDecoration: `none`}}>
              <Nav.Link as="span" eventKey="page-2">
                
              </Nav.Link>
            </Link>
            <Link to="/guide" style={{textDecoration: `none`}}>
              <Nav.Link as="span" eventKey="page1">
                
              </Nav.Link>
            </Link>
            <Link to="/waytocome" style={{textDecoration: `none`}}>
              <Nav.Link as="span" eventKey="waytocome">
                
              </Nav.Link>
            </Link>
            <Link to="/gallery" style={{textDecoration: `none`}}>
              <Nav.Link as="span" eventKey="gallery">
                
              </Nav.Link>
            </Link>
          </Nav>        
        </Navbar.Collapse>
      </Navbar>
      </header>  
    </>
  )
}

export default CustomNavbar